@import '../base';

.c-cart-policies {
  max-width: 107.5rem;
  margin: auto;

  &__block {
    margin: 2.375rem 2.5rem;
    text-align: center;

    &-title {
      color: $s-color--blue-green;
      padding-bottom: 1rem;
    }
  }

  @include t-media-query($s-small-up) {
    display: flex;
    align-items: stretch;

    &__block {
      width: 50%;
    }
  }

  @include t-media-query($s-medium-up) {
    &__block {
      margin: 0;
      padding: 4.25rem 5.25rem;

      &-title { padding-bottom: 1.375rem; }
    }
  }

  @include t-media-query($s-large-up) {
    &__block {
      margin: 0;
      padding: 8.5625rem 10.8125rem;
    }
  }
}